.landing{
	position: relative;
	height: 100vh;
	width: 100vw;
	overflow: hidden;
	background: lighten($mainColor,35%);
}
.landing__text{
	position: absolute;
	top:50%;
	left: 50%;
	transform:translateX(-50%) translateY(-50%);
	text-align: center;
	a{
		color:$mainColor;
	}
}



